.hero{
    position: relative;
/* margin-top: 50px; */
/* margin-bottom: -50px; */
    width: 99vw;
    height: auto;
display: flex;
justify-content: center;
align-items: center;
height: 100vh;
/* width: 100vw; */
overflow: hidden;
background-color: black;
background-attachment: fixed;
}
.video{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    /* height: 100%; */
    padding: 0;
    margin: 0;
    z-index: 1;
   
   
   opacity: 0.7;
   /* box-shadow: inset 0px 0px 155px 0px rgba(0,0,0,0.75); */
}



.heroWrap{
    position: relative;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    width: 1150px;
    height: 100%;
    z-index: 5;
    /* background-color: antiquewhite; */
}
.logo{
  position: absolute;
  width: 700px;
  top: 50%;
  right: -100px;
  transform: translateY(-50%);
  z-index: 50;
}
.leftSide{
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    right: 50%;
    background-color: black;
    z-index: 10;
    background-color: rgba(0, 0, 0, 1);
    /* backdrop-filter: blur(30px); */
    /* box-shadow:  0px 0px 10px 0px #494343bf; */
}